import common from "../../../common/common.js"
var serverUrl =  common.serverUrl
export default{
	name:"BottomBar",
	props:{
		Post:Object,
		edit:{
			type: Boolean,
			default: false
		}
	},
	data(){
		return{
			userId:"",
			userInfo:{},
			token:'',
			type:'',
		}
	},
	//TODO 子组件不可以用onLoad(),但可用created()函数
	created() {
		var userInfo = uni.getStorageSync("globalUser")
		if(userInfo!=null&&userInfo!=""&&userInfo!=undefined){
			this.userInfo=userInfo
			this.token="Bearer "+this.userInfo.token
			this.type=this.userInfo.tokenType
			this.userId=this.userInfo.userId
		}
	},

	methods:{
		send() {
			if (this.edit) {

			} else {
				uni.request({
					header:{
						"Authorization":this.token,
						"type":this.type
					},
					url:serverUrl+'/posts/',
					data:{
						"userId":this.Post.userId,
						"content":this.Post.content,
						"type":this.Post.type,
						"tagIds":this.Post.tagIds,
						"location":this.Post.location,
					},
					method:'POST',
					success: (res) => {
						if(res.data.code===0){
							var currentPostId=res.data.data
							var imgs=this.Post.imgUrls
							if(imgs.length>0){
								for (let i = 0; i < imgs.length; i++) {
									uni.uploadFile({
										header:{
											"Authorization":this.token,
											"type":this.type
										},
										url:serverUrl+'/oss/insert?parentId='+currentPostId,
										filePath:imgs[i],
										name:"file",
										method:'POST',
										success: (response) => {

											uni.showToast({
												title:"发布成功!",
												duration:2000,
											})
											// uni.navigateBack({delta:1})
											uni.redirectTo({
												url:'../post/post'
											})
										},
									})
								}}else{
								uni.showToast({title:"发布成功!",duration:1200})
								uni.navigateBack({delta:1})
							}
							//
						}
					},

				});
			}
	}
	}

}
